.container {
  border: 5px solid #ffcc5c;
  padding: 0;
  display: flex;
  > li {
    flex: 1;
    padding: 10px;
    text-align: center;
    font-size: 2em;
    color: #ffeead;
    box-sizing: border-box;
    background-color: #96ceb4;
    list-style-type: none;
  }
}

.search-input {
  background: #ff6e68;
  border: 0;
  width: 100%;
  outline: 0;
  background: transparent;
  border-bottom: 1px solid #ffeead;
  color: #ffeead;
  :focus {
    outline: none;
  }
}

.nav_logo:hover img {
  -ms-transform: none;
  transform: none;
}

.nav_link-logo img {
  width: 20px;
  margin: 0;
}

.active-tab {
  background-color: orange
}




